#navbar {
    /* border: solid 1px red; */
    width: 100%;
    padding: 25px 0;
    background: #0E141D;
    color: white;
    z-index: 1;
}

#navbar ul {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: center;
    list-style: none;
    padding-left: 15%;
} 

#logo {
    position: absolute;
    top : 0%;
    left : 2%;
    width: 10% ;
    max-width: 150px;
    min-width: 100px;
}

.burgerIcon {
    display:none;
}

@media (max-width: 600px){
    #navbar{
        position: fixed;
        display: flex;
        flex-direction: row;
        width: 100vw;
    }

    .burgerIcon {
        position: relative;
        display: block;
        border: none;
        width: 40px;
        height: 40px;
        border-radius: 0;
        left: 19px;
        top: -10px;
        background: black;
    }

    .burgerIcon .line,
    .burgerIcon .line:after,
    .burgerIcon .line:before{
        position: relative;
        display: block;
        width: 100%;
        height: 2px;
        background: white;
    }

    .burgerIcon .line:after{
        content:'';
        top: -15px;
    }

    .burgerIcon .line:before{
        content:'';
        top: 13px;
    }

    #navbar ul {
        display: none;
        position: relative;
        flex-direction: column;
        width:100%;
        margin-top: 10%;
        right: 5%;
        padding: 0;
    }

    /* .navbar ul {
        display: none;
    } */

    .active ul {
        display: block !important;
    }

    .navbar li {
        flex-direction: column;
        text-align: center;
        padding: 5%;
    }

    #logo {
        left: auto;
        right: 2%;
        top: 2%;
        min-width: 75px;
        max-width: 75px;
    }


    



}

